:root {
  --primary-text-color: #757575;
  --second-text-color: #212121;
  --primary-bg-color: #fff;
  --second-bg-color: rgb(233, 233, 233);
  --table-border-color: rgb(237, 242, 245);
}

body {
  background-color: rgba(231, 231, 231, 0.959);
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5%;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 10px;
  text-align: center;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  list-style: none;
}
